"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { FaSearch } from "react-icons/fa";

export default function Header() {
  const [sticky, setSticky] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  // Sticky navbar effect
  useEffect(() => {
    const handleScroll = () => {
      setSticky(window.scrollY > 80);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header className="header-area header-style-3">
      {/* Header Middle */}
      <div className="header-middle">
        <div className="container-fluid">
          <div className="row align-items-center">
            {/* Left Text (desktop only) */}
            <div className="col-lg-5 d-none d-lg-block">
              <p>
                Welcome to B.S. surgical suppliers. We provide medical
                accessories
              </p>
            </div>

            {/* Contact */}
            <div className="col-xl-2 col-lg-3 col-md-4 col-6">
              <div className="header-area-right">
                <div className="profile">
                  <a href="tel:1234567890">
                    <i className="icon-contact"></i>
                    <span>(+800) 1234 5678 90</span>
                  </a>
                </div>
              </div>
            </div>

            {/* Address (desktop only) */}
            <div className="col-lg-3 ms-auto d-none d-lg-block text-end">
              <div className="middle-r">
                <p>Tripureshwor, Kathmandu</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Menu Area */}
      <div id="header-sticky" className={`menu-area ${sticky ? "sticky" : ""}`}>
        <div className="container-fluid">
          <div className="second-menu">
            <div className="row align-items-center">
              {/* Logo */}
              <div className="col-xl-3 col-lg-3 col-6">
                <div className="navbar-header">
                  <Link href="/" className="navbar-brand">
                    <Image
                      src="/img/logo.png"
                      alt="logo"
                      width={180}
                      height={60}
                      priority
                    />
                  </Link>
                </div>
              </div>

              {/* Desktop Menu */}
              <div className="col-xl-7 col-lg-6 d-none d-lg-block">
                <nav className="main-menu">
                  <ul className="nav">
                    <li className="has-submenu">
                      <Link href="/">Home Pages</Link>
                      <ul className="sub-menu">
                        <li>
                          <Link href="/">Home Page 01</Link>
                        </li>
                        <li>
                          <Link href="/index-2">Home Page 02</Link>
                        </li>
                        <li>
                          <Link href="/index-3">Home Page 03</Link>
                        </li>
                      </ul>
                    </li>
                    <li className="has-submenu">
                      <Link href="/shop">Shop</Link>
                      <ul className="sub-menu">
                        <li>
                          <Link href="/shop">Shop Page</Link>
                        </li>
                        <li>
                          <Link href="/product-single">Shop Single</Link>
                        </li>
                        <li>
                          <Link href="/product-single-s2">Shop Single S2</Link>
                        </li>
                        <li>
                          <Link href="/cart">Cart Page</Link>
                        </li>
                        <li>
                          <Link href="/wishlist">Wishlist</Link>
                        </li>
                        <li>
                          <Link href="/compare">Compare</Link>
                        </li>
                        <li>
                          <Link href="/checkout">Checkout</Link>
                        </li>
                      </ul>
                    </li>
                    <li className="has-submenu">
                      <Link href="/about">Pages</Link>
                      <ul className="sub-menu">
                        <li>
                          <Link href="/about">About</Link>
                        </li>
                        <li>
                          <Link href="/login">My Account</Link>
                        </li>
                        <li>
                          <Link href="/forgot">Forgot Password</Link>
                        </li>
                        <li>
                          <Link href="/compare">Compare</Link>
                        </li>
                        <li>
                          <Link href="/contact">Contact</Link>
                        </li>
                        <li>
                          <Link href="/404">404</Link>
                        </li>
                      </ul>
                    </li>
                    <li className="has-submenu">
                      <Link href="/blog">Blog</Link>
                      <ul className="sub-menu">
                        <li>
                          <Link href="/blog">Blog Grid</Link>
                        </li>
                        <li>
                          <Link href="/blog-right-sidebar">
                            Blog Right Sidebar
                          </Link>
                        </li>
                        <li>
                          <Link href="/blog-left-sidebar">
                            Blog Left Sidebar
                          </Link>
                        </li>
                        <li>
                          <Link href="/blog-fullwidth">Blog Fullwidth</Link>
                        </li>
                        <li className="third-lavel-menu">
                          <Link href="/blog-single">Blog Single</Link>
                          <ul className="sub-menu">
                            <li>
                              <Link href="/blog-single">
                                Blog single right sidebar
                              </Link>
                            </li>
                            <li>
                              <Link href="/blog-single-left-sidebar">
                                Blog single left sidebar
                              </Link>
                            </li>
                            <li>
                              <Link href="/blog-single-fullwidth">
                                Blog single fullwidth
                              </Link>
                            </li>
                          </ul>
                        </li>
                      </ul>
                    </li>
                    <li>
                      <Link href="/contact">Contact</Link>
                    </li>
                  </ul>
                </nav>
              </div>

              {/* Search (desktop only) */}
              <div className="col-xl-2 col-lg-3 d-none d-lg-block">
                <div className="search__container">
                  <input
                    className="search__input"
                    type="text"
                    placeholder="Search products..."
                  />
                  <button className="search__btn" type="submit">
                    <FaSearch />
                  </button>
                </div>
              </div>

              {/* Mobile Menu Button (mobile only) */}
              <div className="col-6 d-lg-none text-end">
                <button
                  className="mobile-menu-toggle"
                  onClick={() => setMobileMenuOpen(true)}
                >
                  ☰
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile Nav Overlay */}
      <div
        className={`mobile-nav-overlay ${mobileMenuOpen ? "active" : ""}`}
        onClick={() => setMobileMenuOpen(false)}
      ></div>

      {/* Mobile Nav Drawer */}
      <div className={`mobile-nav ${mobileMenuOpen ? "active" : ""}`}>
        <button
          className="mobile-close"
          onClick={() => setMobileMenuOpen(false)}
        >
          ✕
        </button>
        <ul>
          <li>
            <Link href="/" onClick={() => setMobileMenuOpen(false)}>
              Home
            </Link>
          </li>
          <li>
            <Link href="/shop" onClick={() => setMobileMenuOpen(false)}>
              Shop
            </Link>
          </li>
          <li>
            <Link href="/about" onClick={() => setMobileMenuOpen(false)}>
              About
            </Link>
          </li>
          <li>
            <Link href="/contact" onClick={() => setMobileMenuOpen(false)}>
              Contact
            </Link>
          </li>
        </ul>
      </div>
    </header>
  );
}
